 $(function () {
    $('.header__btn').on('click', function () {
      $('.rightside-menu').removeClass('rightside-menu--close');
    });
    $('.rightside-menu__close').on('click', function () {
      $('.rightside-menu').addClass('rightside-menu--close');
    });
    $('.top__slider').slick({
      dots: true,
      arrows: true,
      autoplay: true,
    });
    $('.article-slider__box').slick({
      prevArrow: '<button type="submit" class="article-slider__arrow article-slider__arrowLeft"><img src="./img/slider-left.svg" alt="qrrow left"></button>',
      nextArrow: '<button type="submit" class="article-slider__arrow article-slider__arrowRight"><img src="./img/slider-rigth.svg" alt="arrow right"></button>',
      autoplay: true
    });
    var mixer = mixitup('.gallery__inner', {
      load: {
        filter: '.living'
      }
    });
  });

